import Component from "../../../core/Component/Component";
import { ComponentConfigDefaults } from "../../../core/Component/ComponentConfig";
export default class Navbar extends Component {
    constructor(element, config = {}) {
        let cfg = Object.assign(Object.assign({}, ComponentConfigDefaults), config);
        super(element, cfg);
        this.initEvents();
    }
    initEvents() {
        const menuTriggerButtons = this.element.querySelectorAll(".menu-trigger");
        for (const button of menuTriggerButtons) {
            const target = button.dataset["target"];
            button.addEventListener("click", () => {
                this.toggleMenu(button, target);
            });
        }
    }
    toggleMenu(triggerButton, id) {
        let targetElement = (id ? this.element.querySelector(`#${id}`) : this.element.querySelector(".menu"));
        if (targetElement) {
            if (targetElement.classList.contains("open")) {
                targetElement.classList.remove("open");
            }
            else {
                targetElement.classList.add("open");
            }
        }
    }
}
Navbar.className = "navbar";
Navbar.defaultSelector = ".navbar";
